@import "../../../less/Mixins";
.Project {
  position: relative;
  .flex();
  font-size: 1.2rem;
  background: #8e81f1;
  border-radius: 10px;
  width: 100%;
  transition: all .5s ease;
  &  p {
    margin: 0;
    margin-bottom: 1rem;
    &:last-child {
      margin-bottom: 0;
    }
  }
  & h1 {
    word-break: break-all;
    color: black;
    padding: 3rem;
    margin: 0;
  }
  &:hover {
    transform: scale(1.05);
    &:before{
      .animated(choosenProject);
      animation-duration: 1.5s;
    }
  }
  &:before{
    content: '';
    width: 5px;
    height: 100%;
    background: beige;
    position: absolute;
    top: 0;
    left: 0;
    opacity: 0;
    border-radius: 100%;
  }
}
input{
  .todos() ;
  padding-left: 1.5rem;
}
select{
  .todos()
}
.ProjectPage{
  max-width: 1200px;
  margin: 0 auto;
  &__Button{
    transform: translateY(-50%);
    .flex();
    & > button{
      .button();
      transition-duration: 0.15s;
      &:hover{
        transform: scale(1.1);
      }
    }
  }
}

@keyframes glow {
  0%{
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #6e495d, 0 0 40px #ffdb4d, 0 0 50px #ffdb4d, 0 0 60px #ffdb4d, 0 0 70px #ffdb4d;
  }
  100%{
    box-shadow: 0 0 20px #fff, 0 0 30px #ffdb4d, 0 0 40px #ffdb4d, 0 0 50px #ffdb4d, 0 0 60px #ffdb4d, 0 0 70px #ffdb4d, 0 0 80px #ffdb4d;
  }
}
@keyframes choosenProject {
  0%{
    border-radius: 100%;
    opacity: 1;
    left: 0;
    box-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px #6e495d, 0 0 40px #ffdb4d, 0 0 50px #ffdb4d, 0 0 60px #ffdb4d, 0 0 70px #ffdb4d;
  }
  100%{
    top: 3%;
    height: 90%;
    opacity: 1;
    left: 100%;
    box-shadow: 0 0 20px #fff, 0 0 30px #ffdb4d, 0 0 40px #ffdb4d, 0 0 50px #ffdb4d, 0 0 60px #ffdb4d, 0 0 70px #ffdb4d, 0 0 80px #ffdb4d;
  }

}
.Projects {
  padding: 1rem;
  display: grid;
  gap: 1rem;
  grid-template-rows: 1fr;
  grid-auto-flow: row;
}
.ProjectPage__form{
  height: 100%;
  .flex();
  flex-direction: column;
  & button{
    margin-top: 5rem;
    .button();
    transition-duration: 0.15s;
    &:hover{
      transform: scale(1.05);
    }
    &:disabled{
      background: gray;
    }
  }
  & input{
    width: 100%;
    margin: 0;
  }
}

.FormDate input {
  width: 50px;
  font-size: 1rem;
  padding-left: 0.5rem;

}